.hownd-checklist-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}
.hownd-checklist-grid__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  flex: 0 0 100%;
  max-width: 100%;
}
.hownd-checklist-grid .check-icon {
  margin-bottom: 20px;
}
.hownd-checklist-grid__title {
  color: #ffa601;
}
.hownd-checklist-grid__text {
  margin-bottom: 30px;
}
.hownd-checklist-grid__image {
  margin-top: auto;
}
.hownd-checklist-grid__image img {
  width: auto;
  max-width: 130px;
  height: 121px;
  object-fit: contain;
}
@media screen and (min-width: 768px) {
  .hownd-checklist-grid__item {
    flex: 0 0 33.33%;
    max-width: 33.33%;
  }
  .hownd-checklist-grid__text {
    margin-bottom: 50px;
  }
}